.resum-container {
  display: flex;
  width: 80%;
  height: 100vh;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  .top {
    h2 {
      text-align: center;
      margin-bottom: 8px;
      color: greenyellow;
      &:hover {
        color: burlywood;
      }
    }
    margin-bottom: 1.5rem;
  }
  .bottom {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    .blog1,
    .blog2 {
      h3{
        font-size: 18px;
      }
      p {
        line-height: 1.5rem;
        font-size: 15px;
      }
      background-color: aliceblue;
      margin: 0.5rem;
      padding: 1rem;
      border-radius: 8px;
      color: black;
      margin-bottom: 1rem;
    }
  }
}
@media screen and (max-width: 900px) {
  .resum-container {
    height: 80vh;
    .top {
      h2 {
        text-align: center;
        color: greenyellow;
        margin-top: 5rem;
        font-size: 18px;
        &:hover {
          color: burlywood;
        }
      }
      h3{
        display: none;
      }
      p {
        display: none;
      }
    }
    .bottom {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      flex-wrap: wrap;
      gap: 0.5rem;
      .blog1,
      .blog2 {
        h3{
          font-size: 15px;
        }
        p{
          font-size: 12px;
          line-height: 1rem;
        }
        background-color: aliceblue;
        padding: 1rem;
        border-radius: 0.5rem;
        color: black;
      }
    }
  }
}
