.service-container {
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  display: flex;
  width: 80%;
  margin: 0 auto;
  height: 100vh;
  justify-content: center;
  align-items: center;
  .top {
    h2 {
      text-align: center;
      margin-bottom: 10px;
      color: greenyellow;
      &:hover {
        color: burlywood;
      }
    }
  }
  .bottom {
    display: flex;
    align-items: center;
    justify-content:space-between;
    gap: 1rem;   
    .box {
      background-color: aliceblue;
      padding: 0.5rem;
      color: black;
      border-radius: 1rem;
      h2{
        font-size: 18px;
      }
      p{
        line-height: 1.5rem;
      }
    }
  }
}
@media screen and (max-width: 900px) {
  .service-container {
    .top {
      h2 {
        text-align: center;
        color: greenyellow;
        font-size: 18px;
        &:hover {
          color: burlywood;
        }
      }
      p {
        display: block;
        font-size: 12px;
      }
    }
    .bottom {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      .box{
        padding: 0.5rem;
        h2{
          font-size: 14px;
        }
        p{
          font-size: 12px;
          line-height: 1rem;
        }
      }
    }
  }
}
