.container {
  display: flex;
  width: 80%;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  gap: 10rem;
  z-index: -1;
  .left-side {
    display: flex;
    flex-direction: column;
    gap: 3rem;
    margin-top: 15rem;
    .title {
      cursor: pointer;
      color: white;
      font-size: 3rem;
      font-weight: 900;
      font-style: oblique;
      font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
        Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
        sans-serif;
      span {
        color: greenyellow;
      }
    }
    .desc {
      p {
        line-height: 1.8rem;
        font-size: 18px;
        font-weight: 600;
      }
    }
  }
  .right-side {
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: -1;
    margin-top: 1rem;
    .img1 {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 13rem;
        height: 15rem;
        margin-top: 6rem;
        border-radius: 50%;
        padding: 2rem;
      img {
        width: 28rem;
        height: 28rem;
      }
    }
    .img2 {
      display: none;
    }
  }
}

@media screen and (max-width: 900px) {
  .container {
    gap: 0rem;
    .right-side {
      margin-top: 10rem;
      .img1 {
        display: none;
      }
      .img2 {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 0;
        img {
          width: 150px;
          height: 150px;
          border-radius: 50%;
        }
      }
    }
    .left-side {
      gap: 1rem;
      margin-top: 10rem;
      .title {
        font-size: 1.3rem;
        margin: 0;
      }
      .desc {
        p {
          font-size: 12px;
          line-height: 1.2rem;
        }
      }
    }
  }
}
