.top-container {
  display: flex;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  .top {
    margin-top: 4rem;
    h2 {
      text-align: center;
      margin-bottom: 20px;
    }
    margin-bottom: 0.5rem;
  }
  .bottom {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    .talktome {
      display: flex;
      align-items: center;
      flex-direction: column;
      padding: 10px;
      width: 30%;
      border-radius: 8px 0 0 8px;
      background-color: rgb(67, 58, 58);
      height: 95%;
      h3 {
        margin-bottom: 10px;
      }
      .talksection {
        display: flex;
        flex-direction: column;
        gap: 20px;
        .talkItems {
          display: flex;
          flex-direction: column;
          align-items: center;
          background-color: aliceblue;
          color: black;
          cursor: pointer;
          padding: 8px;
          border-radius: 8px;
          gap: 10px;
          a {
            text-decoration: none;
          }
        }
      }
    }
    .contact-form {
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: aliceblue;
      flex-direction: column;
      padding: 10px;
      width: 30%;
      height: 95%;
      border-radius: 0 8px 8px 0;
      h3 {
        color: black;
        margin-bottom: 10px;
      }
      form {
        .input {
          input {
            border: 0;
            padding: 10px 20px;
            border-radius: 10px;
          }
          textarea {
            border: 0;
            padding: 10px 20px;
            border-radius: 10px;
          }
          margin-bottom: 1rem;
        }
        button {
          padding: 8px 12px;
          border-radius: 10px;
          border: 0;
          color: white;
          background-color: blue;
          cursor: pointer;
        }
      }
    }
  }
}
@media screen and (max-width: 900px) {
  .top-container {
    height: 80vh;
    margin-top: 2rem;
    .top{
      margin-top: 0;
      h2{
        font-size: 18px;
      }
    }
    .bottom {
      .talktome {
        display: none;
      }
      .contact-form {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: aliceblue;
        padding: 20px;
        margin: 15px;
        height: 95%;
        width: 70%;
        border-radius: 10px;
        h3{
          font-size: 15px;
        }
        button{
          font-size: 12px;
        }
      }
    }
  }
}
