.about__container {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  flex-direction: column;
  width: 80%;
  margin-top: 3rem;
  .about-items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 1.5rem;
    margin-top: 3rem;
    .left-side {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10rem;
      .left-info {
        h2 {
          margin-bottom: 2rem;
          color: lawngreen;
          &:hover {
            color: burlywood;
          }
        }
        p {
          line-height: 1.5rem;
          margin-top: 1rem;
          font-size: 18px;
        }
      }
      .left-img {
        margin-top: 6rem;
        img {
          width: 300px;
          height: 300px;
          border-radius: 50%;
        }
      }
    }
    .right-side {
      width: 25rem;
      justify-content: space-between;
      .icons {
        background-color: aliceblue;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        color: black;
        padding: 10px 20px;
        border-radius: 8px;
      }
    }
    .cv {
      margin-top: 20px;
      button {
        background-color: blue;
        color: white;
        padding: 10px 20px;
        border-radius: 8px;
        border: none;
        width: 8rem;
        cursor: pointer;
      }
    }
  }
}
@media screen and (max-width: 900px) {
  .about__container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    margin-top: 0;
    height: 60vh;
    .about-items {
      margin-top: 0;
      .left-side {
        .left-info {
          h2 {
            font-size: 18px;
          }
          p {
            font-size: 12px;
          }
        }
        .left-img {
          display: none;
        }
      }
      .right-side {
        width: 10rem;
      }
    }
  }
}
